import React, { useEffect, useState } from 'react'
import { Button, Grid, Box, TextField, } from '@mui/material'
import Input from '@mui/material/Input';
import MetaData from '../../MetaData/MetaData'
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from "react-redux";
import { clearErrors, contactForm, reloadUser } from "../../actions/userAction";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './contact.css';


const Contact = () => {
  const { error, message, loading, isAuthenticated } = useSelector((state) => state.contact);
  const BoxStyle = { padding: 20, display: 'flex', margin: '10px auto', flexDirection: 'column' }
  const loginStyle = { display: 'flex', alignItems: 'center', justifyContent: 'center' }
  const GridStyle = { margin: '20px auto', alignItems: 'center' }
  const TitleStyle = { marginTop: '0px', fontSize: '24px', color: ' rgb(1, 114, 114)', fontWeight: '600', fontFamily: 'sans-serif' }
  const InputStyle = { marginTop: '20px', fontSize: '17px', color: '#000', fontWeight: '500' }
  const ButtonStyled = { marginTop: '15px', fontSize: '15px', fontWeight: '600', backgroundColor: ' teal' }
  const MapStyled = { width: "100%", height: "400px", border: "0", allowfullscreen: "", loading: "lazy", referrerpolicy: "no-referrer-when-downgrade" }

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.user);
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [messages, setMessages] = useState("");

  const handleSubmitForm = (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append('name', name)
    formData.append('email', email);
    formData.append('message', messages);
    dispatch(contactForm(formData));
  }

  useEffect(() => {
    if (error) {
      toast.error(error);
      dispatch(clearErrors());
    }

    if (message) {
      toast.success(message);
      // navigate('/')
     window.location.reload('/contact')
    }
  }, [dispatch, error, message, isAuthenticated]);

  return (
    <>
      <MetaData title="Contact Us" />
      {/* google map */}
      <Box>
        <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d227748.382561951!2d75.65046950088119!3d26.885447918213902!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x396c4adf4c57e281%3A0xce1c63a0cf22e09!2sJaipur%2C%20Rajasthan!5e0!3m2!1sen!2sin!4v1673426681422!5m2!1sen!2sin" style={MapStyled}></iframe>
      </Box>
      <Grid item xs={12} sm={6} md={4} style={loginStyle}>
        <Box style={BoxStyle}>
          <Grid style={GridStyle}>
            <h2 style={TitleStyle}>Let's get In touch with us!</h2>
          </Grid>
          <form onSubmit={handleSubmitForm}>
            <Input
              style={InputStyle}
              label='name'
              placeholder='Enter Your Name...'
              type='name'
              fullWidth
              required
              onChange={(e) => setName(e.target.value)}
            />
            <Input
              style={InputStyle}
              label='Email'
              placeholder='Enter Your Email...'
              type='email'
              fullWidth
              required
              onChange={(e) => setEmail(e.target.value)}
            />
            <TextField
              fullWidth
              placeholder='Type Your Message Here...'
              multiline
              rows={5}
              autoComplete="none"
              required
              className='TextFieldStyle'
              onChange={(e) => setMessages(e.target.value)}
            >
            </TextField>
            <Button style={ButtonStyled} type='submit' variant='contained'>Submit </Button>
          </form>
        </Box>
      </Grid>
      <ToastContainer
        position="bottom-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="dark"
      />
    </>
  )
}

export default Contact