import React, { useEffect, useState } from 'react'
import { Avatar, Button, Grid, Paper, Typography } from '@mui/material'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Input from '@mui/material/Input';
import InputAdornment from '@mui/material/InputAdornment';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import IconButton from '@mui/material/IconButton';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import MetaData from '../../MetaData/MetaData'
import { useDispatch, useSelector } from "react-redux";
import { clearErrors, recoverAccountLogin } from "../../actions/userAction";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './account.css'

const RecoverAccountLogin = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const { error, success, loading, isAuthenticated } = useSelector(
        (state) => state.forgotPassword
    );

    const [showPassword, setShowPassword] = React.useState(false);
    const handleClickShowPassword = () => setShowPassword((show) => !show);
    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };
    const loginStyle = { display: 'flex', alignItems: 'center', justifyContent: 'center' }
    const paperStyle = { backgroundColor: 'white', padding: 20, width: 300, display: 'flex', margin: '20px auto', flexDirection: 'column' }
    const GridStyle = { margin: '20px auto', alignItems: 'center' }
    const AvatarStyle = { backgroundColor: 'teal', marginLeft: '42%' }
    const TitleStyle = { marginTop: '20px', fontSize: '24px', color: ' rgb(1, 114, 114)', fontWeight: '600', fontFamily: 'sans-serif' }
    const InputStyle = { marginTop: '20px', fontSize: '17px', color: '#000', fontWeight: '500' }
    const ButtonStyled = { marginTop: '15px', fontSize: '15px', fontWeight: '600', backgroundColor: ' teal' }

    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    const location = useLocation();
    const token = location.pathname.split("/")[3]   // get the url token 

    console.log(token, "token 43" )
    const recoverAccountLoginSubmit = (e) => {
        e.preventDefault();
        dispatch(recoverAccountLogin(token, username, password));
    };

    useEffect(() => {
        if (error) {
            toast.error(error);
            dispatch(clearErrors());
        }

        if (success) {
            toast.success("Your Account Recover Successfully");
            navigate('/profile');
        }
        
        if (isAuthenticated) {
            navigate('/profile');
        }
    }, [dispatch, error, success, isAuthenticated]);

    return (
        <>
            <MetaData title="Recover Account Login" />
            <Grid item xs={12} sm={6} md={4} style={loginStyle} className="MobileLogin" >
                <div style={paperStyle}>
                    <Grid style={GridStyle}>
                        <Avatar style={AvatarStyle}><LockOutlinedIcon style={{ fontSize: '2.4rem' }} /></Avatar>
                        <h2 style={TitleStyle} >Recover Account Login</h2>
                    </Grid>
                    <form onSubmit={recoverAccountLoginSubmit}>
                        <Input
                            style={InputStyle}
                            label='Username'
                            placeholder='Enter Your Username...'
                            type='text'
                            fullWidth
                            required
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                        />
                        <Input
                            style={InputStyle}
                            label='confirmPassword'
                            placeholder='Enter Your Confirm Password...'
                            fullWidth
                            type={showPassword ? 'text' : 'password'}
                            required
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            autoComplete="new-password"
                            endAdornment={
                                <InputAdornment position="end">
                                    <IconButton
                                        aria-label="toggle password visibility"
                                        onClick={handleClickShowPassword}
                                        onMouseDown={handleMouseDownPassword}
                                        edge="end"
                                    >
                                        {showPassword ? <VisibilityOff style={{ fontSize: '2.3rem' }} /> : <Visibility style={{ fontSize: '2.4rem' }} />}
                                    </IconButton>
                                </InputAdornment>
                            }
                        />

                        <Button
                            style={ButtonStyled}
                            fullWidth
                            type='submit'
                            color="primary"
                            variant='contained'>
                            Confirm Password
                        </Button>

                        <Typography
                            style={{ fontSize: '13px', marginTop: '10px', fontWeight: '600' }}
                            className="MobileText">
                            Do you have an account ?
                            <Link to='/login'
                                style={{ marginLeft: '8px', color:'teal' }}>
                                Sign In
                            </Link>
                        </Typography>

                    </form>
                </div>
            </Grid>
            <ToastContainer
                position="bottom-right"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="dark"
            />
        </>
    )
}

export default RecoverAccountLogin