import React from 'react'
import { Avatar, Box, Card, Grid, styled, Typography } from '@mui/material'
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import { Link } from 'react-router-dom';
import "./post.css";

const BlogContainer = styled(Box)`
    padding-top: 10px;
    margin-left: 20px;
    margin-right: 20px;
`

const CardFooter = styled(CardActions)`
   display: flex;
   margin: 0 10px;
   justify-content:space-between;
`
const CardProfile = styled(Box)`
    display: flex;
`

const Post = ({ post }) => {
    const blogImageUrl = post.image ? post.image : 'https://img.freepik.com/free-vector/winter-forest-with-frozen-river_107791-4097.jpg'

    return (
        <>
            <BlogContainer>
                <Grid container spacing={3}>
                    <Grid item xs={12} sm={12} md={12} >
                        <Card sx={{ maxWidth: '100%' }}>
                            <CardMedia
                                sx={{ height: 240 }}
                                image={blogImageUrl}
                                title="blog post image"
                                alt="post image"
                            />
                            <CardContent>
                                <Typography className="postTitle">
                                    {post.title}
                                </Typography>
                                <Typography className="postDesc">
                                    {post.desc}
                                </Typography>
                            </CardContent>
                            <CardFooter>
                                <CardProfile>
                                    <Avatar src={post?.senderId?.profile} alt={post?.senderId?.username} />
                                    <Box ml={2}>
                                        <Typography className='postName'>
                                            <Link to={`/?user=${post.senderId.username}`} style={{ textDecoration: 'none', color: 'inherit', cursor: 'pointer' }}>{post?.senderId?.username}</Link>
                                        </Typography>
                                        <Typography className='postDate'>
                                            {new Date(post.createdAt).toDateString()}
                                        </Typography>
                                    </Box>
                                </CardProfile> 
                                <Box >
                                    <Link to={`/post/${post._id}`} ><ArrowForwardIcon style={{ fontSize: '2.5rem', color: '#878787' }} titleAccess='Read More' /> </Link>
                                </Box>
                            </CardFooter>
                        </Card>
                    </Grid>
                </Grid>
            </BlogContainer>

        </>
    )
}

export default Post