import React, { useState } from 'react';
import { Button, Container, FormControl, InputBase, styled, TextareaAutosize } from '@mui/material';
import AddCircleOutlinedIcon from '@mui/icons-material/AddCircleOutlined';
import MetaData from '../../MetaData/MetaData';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { useSelector } from "react-redux";
import Loader from '../../Components/loader/Loader';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Image = styled('img')({
  width: '100%',
  height: '50vh',
  objectFit: 'cover',
  marginTop: '20px'
});

const StyledFormControl = styled(FormControl)`
  margin-top:10px;
  display: flex;
  flex-direction: row;
`;

const InputTextField = styled(InputBase)`
  flex:1;
  margin:0 30px;
  font-size: 25px;
`;

const Textarea = styled(TextareaAutosize)`
  width: 100%;
  margin-top:50px;
  font-size:18px;
  border:none;
  &:focus-visible {
    outline:none;
  }
`;

const Write = () => {
  const { isAuthenticated, user } = useSelector((state) => state.user);
  const url = 'https://helpx.adobe.com/content/dam/help/en/photoshop/using/convert-color-image-black-white/jcr_content/main-pars/before_and_after/image-before/Landscape-Color.jpg'
  const TitleStyle = { marginTop: '20px', fontSize: '24px', color: ' rgb(1, 114, 114)', fontWeight: '600', fontFamily: 'sans-serif' }
  
  const navigate = useNavigate();
  const [title, setTitle] = useState("");
  const [desc, setDesc] = useState("");
  const [image, setImage] = useState("");
  const [filename, setFilename] = useState('https://helpx.adobe.com/content/dam/help/en/photoshop/using/convert-color-image-black-white/jcr_content/main-pars/before_and_after/image-before/Landscape-Color.jpg');
  const [loading, setLoading] = useState(false)
  
  const writeImage = (e) => {
    setImage(e.target.files[0]);
    setFilename(e.target.files[0].name);
  }

  const handleWriteSubmit = async (e) => {
    e.preventDefault();
    //  create post api call
    const formData = new FormData();
    const filename = Date.now() + image.name
    formData.append("image", image);
    formData.append("filename", filename);
    formData.append('title', title);
    formData.append('desc', desc);
    formData.append('username', user.username);
    formData.append("userprofile", user.profile);
    
    setLoading(true)   // loading show 
    try {
      const res = await axios.post( '/api/v1/post', formData,
        {
          headers: {
            "Content-Type": "multipart/form-data"
          },
        });
      setLoading(false)  // loading false 
      navigate("/post/" + res.data.newPost._id);

    } catch (err) {
      setLoading(false) // loading false 
      toast.error(err.response.data.message);
    }
  }

  return (
    <>
      <MetaData title="Write" />
      {loading ? (
        <Loader />
      ) : (
        <Container> 
          <h2 style={TitleStyle} >Write Blog!</h2>
          {image &&(<Image src={URL.createObjectURL(image)}alt='bannerPost' />)||<Image src={url} alt='bannerPost' />}
          <form onSubmit={handleWriteSubmit}>
            <StyledFormControl>
              <label htmlFor='fileInput'>
                <AddCircleOutlinedIcon
                  fontSize='large'
                  color='action' titleAccess='Add Picture' style={{ fontSize: '3.1rem', marginTop: '6px', cursor: 'pointer' }} />
              </label>
              <input
                type='file'
                id='fileInput'
                style={{ display: 'none' }}
                onChange={writeImage}
              />
              <InputTextField
                placeholder='Title'
                name='title'
                required
                onChange={(e) => setTitle(e.target.value)}
              />
              <Button
                variant='contained'
                style={{ fontSize: '1.4rem', backgroundColor: ' teal', fontWeight: '600', fontFamily: 'sans-serif' }}
                type='submit'
              >
                Publish
              </Button>
            </StyledFormControl>
            <Textarea
              minRows={5}
              placeholder="Tell your story...."
              name='description'
              required
              onChange={(e) => setDesc(e.target.value)}
            />
          </form>
        </Container>
      )}
      <ToastContainer
        position="bottom-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="dark"
      />
    </>
  )
}

export default Write