import React, { useEffect, useState } from 'react'
import { Avatar, Button, Grid, Paper, Typography } from '@mui/material'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Input from '@mui/material/Input';
import InputAdornment from '@mui/material/InputAdornment';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import IconButton from '@mui/material/IconButton';
import { Link, useNavigate } from 'react-router-dom';
import MetaData from '../../MetaData/MetaData'
import { useDispatch, useSelector } from "react-redux";
import { clearErrors, login } from "../../actions/userAction";
import Loader from '../../Components/loader/Loader'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './login.css'


const Login = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const { error, loading, user, isAuthenticated } = useSelector((state) => state.user);
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");

    const [showPassword, setShowPassword] = React.useState(false);
    const handleClickShowPassword = () => setShowPassword((show) => !show);
    const handleMouseDownPassword = (event) => { event.preventDefault() };
    const loginStyle = { display: 'flex', alignItems: 'center', justifyContent: 'center', }
    const paperStyle = { padding: 20, width: 300, display: 'flex', margin: '20px auto', flexDirection: 'column', marginTop: '20px' }
    const GridStyle = { margin: '20px auto', alignItems: 'center' }
    const AvatarStyle = { backgroundColor: 'teal', marginLeft: '24px' }
    const TitleStyle = { marginTop: '20px', fontSize: '24px', color: ' rgb(1, 114, 114)', fontWeight: '600', fontFamily: 'sans-serif' }
    const InputStyle = { marginTop: '20px', fontSize: '17px', color: '#000', fontWeight: '500' }
    const ButtonStyled = { marginTop: '15px', fontSize: '15px', fontWeight: '600', backgroundColor: ' teal' }
    const checkboxStyle = { display: 'inline-block', marginTop: '10px' }
    const label = { inputProps: { 'aria-label': 'Checkbox' } };

    // login api call
    const LoginHandleSubmit = async (e) => {
        e.preventDefault();
        dispatch(login(username, password));
    }

    useEffect(() => {
        if (error) { 
            toast.error(error);
            dispatch(clearErrors());
        }
        if (isAuthenticated) {
            navigate('/profile');
        }
    }, [dispatch, error, isAuthenticated]);

    return (
        <>
            <MetaData title="Login" />
            <div className='loginForm'>
            {loading ? (
                <Loader />
            ) : (
                <>
                    <Grid item xs={12} sm={6} md={4} style={loginStyle} className="MobileLogin" >
                        <Paper elevation={10} style={paperStyle}>
                            <Grid style={GridStyle}>
                                <Avatar style={AvatarStyle}><LockOutlinedIcon style={{ fontSize: '2.4rem' }} /></Avatar>
                                <h1 style={TitleStyle} >Sign In</h1>
                            </Grid>
                            <form onSubmit={LoginHandleSubmit}  >
                                <Input
                                    style={InputStyle}
                                    label='Username'
                                    placeholder='Enter Your Username...'
                                    type='text'
                                    fullWidth
                                    required
                                    value={username}
                                    onChange={(e) => setUsername(e.target.value)}
                                />
                                <Input
                                    style={InputStyle}
                                    label='Password'
                                    placeholder='Enter Your Password...'
                                    fullWidth
                                    type={showPassword ? 'text' : 'password'}
                                    required
                                    value={password}
                                    onChange={(e) => setPassword(e.target.value)}
                                    endAdornment={
                                        <InputAdornment position="end">
                                            <IconButton
                                                aria-label="toggle password visibility"
                                                onClick={handleClickShowPassword}
                                                onMouseDown={handleMouseDownPassword}
                                                edge="end"
                                            >
                                                {showPassword ? <VisibilityOff style={{ fontSize: '2.3rem' }} /> : <Visibility style={{ fontSize: '2.4rem' }} />}
                                            </IconButton>
                                        </InputAdornment>
                                    }
                                />
                                 <Typography
                                    style={{ fontSize: '13px', marginTop: '10px', fontWeight: '600' }}
                                    className="MobileText">
                                    <Link to="/forgot/password" style={{ marginLeft: '60%', color:'teal' }}>
                                        Forgot password ?
                                    </Link>
                                </Typography>
                                <Button
                                    style={ButtonStyled}
                                    className={"disabled"}
                                    fullWidth
                                    type='submit'
                                    variant='contained'
                                >
                                    Sign In
                                </Button>
                                <Typography
                                    style={{ fontSize: '13px', marginTop: '12px', fontWeight: '600' }}
                                    className="MobileText">
                                    Do you have an account ?
                                    <Link to='/register'
                                        style={{ marginLeft: '8px', color:'teal' }}>
                                        Sign Up
                                    </Link>
                                </Typography>
                            </form>
                        </Paper>
                    </Grid>
                </>
            )}
            <ToastContainer
                position="bottom-right"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="dark"
            />
            </div>
        </>
    )
}

export default Login








