import React, { useEffect, useState } from 'react'
import { Box, Typography, styled, TextareaAutosize, Button, FormControl, InputBase } from '@mui/material';
import { Delete, Edit } from '@mui/icons-material';
import { AddCircle as Add } from '@mui/icons-material';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { useSelector } from "react-redux";
import axios from 'axios'
import Loader from '../loader/Loader';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Comments from './comments/Comments';


const Container = styled(Box)(({ theme }) => ({
    marginTop: '50px',
    [theme.breakpoints.down('md')]: {
        margin: 0
    },
}));

const Image = styled('img')({
    width: '100%',
    height: '50vh',
    objectFit: 'cover'
});

const EditIcon = styled(Edit)`
    margin: 5px;
    padding: 5px;
    cursor: pointer;
`;

const DeleteIcon = styled(Delete)`
    margin: 5px;
    padding: 5px;
    cursor: pointer;
`;

const Heading = styled(Typography)`
    font-size: 25px;
    font-weight: 550;
    text-align: center;
    margin: 20px 0 0px 0;
`;

const Author = styled(Box)(({ theme }) => ({
    color: '#878787',
    display: 'flex',
    margin: '20px 0',
    [theme.breakpoints.down('sm')]: {
        display: 'block'
    },
}));

const StyledFormControl = styled(FormControl)`
    margin-top: 10px;
    display: flex;
    flex-direction: row;
`;

const InputTextField = styled(InputBase)`
    flex: 1;
    margin: 0 30px;
    font-size: 25px;
`;

const StyledTextArea = styled(TextareaAutosize)`
    width: 100%;
    border: none;
    margin-top: 50px;
    font-size: 18px;
    &:focus-visible {
        outline: none;
    }
`;


const SinglePost = () => {
    const navigate = useNavigate();
    const { user } = useSelector((state) => state.user);
    const [post, setPost] = useState({})
    const location = useLocation();
    const postId = location.pathname.split("/")[2]
    const [title, setTitle] = useState("");
    const [desc, setDesc] = useState("");
    const [filename, setFilename] = useState("");
    const [image, setImage] = useState(null);
    const [name, setName] = useState('');

    const [updateMode, setUpdateMode] = useState(false)
    const [loading, setLoading] = useState(false)
    // get all posts api call 
    useEffect(() => {
        const getPost = async () => {
            const res = await axios.get('/api/v1/post/details/' + postId)
            setPost(res.data)
            setTitle(res.data.title)
            setDesc(res.data.desc)
            setName(res.data.senderId.username)
        }
        getPost();
    }, [postId])

    //   delete post
    const handleSinglePostDelete = async () => {
        try {
            const res = await axios.delete(`/api/v1/post/delete/${post._id}`, {
                data: { username: name},
            })
            setLoading(true)  // loading show 
            toast.error("Your post delete successfully")
            navigate('/');
        } catch (error) {
            setLoading(false)   // loading show 
            toast.error(error)
        }
    }

    const handleUpdatePost = async () => {
        const updatePost = {
            username: user.username,
            title,
            desc,
            image,
            filename,
        };
        if (image) {
            const data = new FormData();
            const filename = Date.now() + image.name;
            data.append("filename", filename);
            data.append("image", image);
            updatePost.filename = image;
        }
        setLoading(true)   // loading show 
        try {
            const res = await axios.put(`/api/v1/post/${post._id}`, updatePost, {
                headers: {
                    "Content-Type": "multipart/form-data"
                }
            })
            setLoading(true)  // loading false 
            toast.success("Your post update successfully");
            navigate('/');

        } catch (error) {
            setLoading(false) // loading false 
            toast.error(error)

        }
    } 

    const url = 'https://helpx.adobe.com/content/dam/help/en/photoshop/using/convert-color-image-black-white/jcr_content/main-pars/before_and_after/image-before/Landscape-Color.jpg'

    return (
        <>
        {loading ? (
            <Loader />
        ): (
            <> 
            <Container>
                <Image src={image ? URL.createObjectURL(image) : post.image || url} alt="post" />
                {/* <a href ={`https://www.facebook.com/sharer/sharer.php?u=https://sanjeevkumarsk.web.app/ ${post._id}`   } ><DeleteIcon color="error" style={{ fontSize: '2.5rem',  }} titleAccess='Delete'/></a>
                <a href ={''} ><DeleteIcon color="error" style={{ fontSize: '2.5rem',  }} titleAccess='Delete'/></a>
                <a href ={''} ><DeleteIcon color="error" style={{ fontSize: '2.5rem',  }} titleAccess='Delete'/></a> */}

                {updateMode ? (
                    <>
                    <StyledFormControl>
                        <label htmlFor="fileInput">
                            <Add
                                fontSize='large'
                                color='action' titleAccess='Add Picture' style={{ fontSize: '3.1rem', marginTop: '6px', cursor: 'pointer' }} />
                        </label>
                        <input
                            type="file"
                            id="fileInput"
                            style={{ display: "none" }}
                            onChange={(e) => setImage(e.target.files[0])}
                        />
                        <InputTextField
                            value={title}
                            name='title'
                            placeholder="Title"
                            onChange={(e) => setTitle(e.target.value)}
                        />
                        <Button
                            variant="contained"
                            color="primary"
                            style={{ fontSize: '1.4rem', backgroundColor: ' teal', fontWeight: '600', fontFamily: 'sans-serif' }}
                            onClick={handleUpdatePost}
                        >
                            Update
                        </Button>
                    </StyledFormControl>
                    </>
                ) : ( 
                    <>
                        <Box style={{ float: 'right' }}>

                            {name === user?.username && (
                                <>
                                    <Link ><EditIcon color="primary" style={{ fontSize: '2.5rem' }} titleAccess='Edit' onClick={() => setUpdateMode(true)} /></Link>

                                    <DeleteIcon color="error" style={{ fontSize: '2.5rem' }} titleAccess='Delete' onClick={handleSinglePostDelete} />
                                </>
                            )}

                        </Box>
                        <Heading>
                            {title}
                        </Heading>
                    </>
                )}

                <>
                    <Author>
                        <Link to={`/?user=${name}`} style={{ textDecoration: 'none', color: 'inherit', cursor: 'pointer' }}>
                            <Typography style={{ fontSize: '1.4rem' }}>Author: <span style={{ fontWeight: 700 }}> {name} </span></Typography>
                        </Link>
                        <Typography style={{ marginLeft: 'auto', fontSize: '1.4rem' }}>{new Date(post.createdAt).toDateString()}</Typography>
                    </Author>

                    {updateMode ? (
                        <StyledTextArea
                            rowsMin={5}
                            placeholder="Tell your story..."
                            name='description'
                            value={desc}
                            onChange={(e) => setDesc(e.target.value)}
                        />
                    ) : (
                        <Typography style={{ fontSize: '18px' }} className="singlePostDesc">
                            {desc}
                        </Typography>
                    )}
                </>
             

                    
                <Comments post={post} />
            </Container>
            </>
        )}
        
        <ToastContainer
                position="bottom-right"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="dark"
            />
            
        </>
    )
}



export default SinglePost