import {
    LOGIN_REQUEST,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    REGISTER_USER_REQUEST,
    REGISTER_USER_SUCCESS,
    REGISTER_USER_FAIL,
    RELOAD_USER_REQUEST,
    RELOAD_USER_SUCCESS,
    RELOAD_USER_FAIL,
    LOGOUT_SUCCESS,
    LOGOUT_FAIL,
    UPDATE_PROFILE_REQUEST,
    UPDATE_PROFILE_SUCCESS,
    UPDATE_PROFILE_FAIL,
    UPDATE_PASSWORD_REQUEST,
    UPDATE_PASSWORD_SUCCESS,
    UPDATE_PASSWORD_FAIL,
    DELETE_USER_REQUEST,
    DELETE_USER_SUCCESS,
    DELETE_USER_FAIL,
    FORGOT_PASSWORD_REQUEST,
    FORGOT_PASSWORD_SUCCESS,
    FORGOT_PASSWORD_FAIL,
    RESET_PASSWORD_REQUEST,
    RESET_PASSWORD_SUCCESS,
    RESET_PASSWORD_FAIL,
    CONTACT_USER_REQUEST,
    CONTACT_USER_SUCCESS,
    CONTACT_USER_FAIL,
    RECOVER_ACCOUNT_REQUEST,
    RECOVER_ACCOUNT_SUCCESS,
    RECOVER_ACCOUNT_FAIL,
    RECOVER_ACCOUNT_LOGIN_REQUEST,
    RECOVER_ACCOUNT_LOGIN_SUCCESS,
    RECOVER_ACCOUNT_LOGIN_FAIL,
    CLEAR_ERRORS,
} from "../constants/userConstants";
import axios from 'axios';

// login
export const login = (username, password) => async (dispatch) => {
    try {
        dispatch({ type: LOGIN_REQUEST });
        const config = { headers: { "Content-Type": "application/json" } };
        const { data } = await axios.post(
            '/api/v1/auth/login',
            { username, password },
            config
        );
        dispatch({ type: LOGIN_SUCCESS, payload: data.user });
    } catch (error) {
        dispatch({ type: LOGIN_FAIL, payload: error.response.data.message });
    }
};

// Register
export const userRegister = (userData) => async (dispatch) => {
    try {
        dispatch({ type: REGISTER_USER_REQUEST });

        const config = { headers: { "Content-Type": "multipart/form-data" } };

        const { data } = await axios.post('/api/v1/auth/register', userData, config);

        dispatch({ type: REGISTER_USER_SUCCESS, payload: data.user });
    } catch (error) {
        dispatch({
            type: REGISTER_USER_FAIL,
            payload: error.response.data.message,
        });
    }
};

// Reload (profile)
export const reloadUser = () => async (dispatch) => {
    try {
        dispatch({ type: RELOAD_USER_REQUEST });

        const { data } = await axios.get('/api/v1/auth/profile');

        dispatch({ type: RELOAD_USER_SUCCESS, payload: data.user });
    } catch (error) {
        dispatch({
            type: RELOAD_USER_FAIL,
            payload: error.response.data.message,
        });
    }
};

// Logout User
export const logout = () => async (dispatch) => {
    try {
        await axios.get('/api/v1/auth/logout');

        dispatch({ type: LOGOUT_SUCCESS });
    } catch (error) {
        dispatch({ type: LOGOUT_FAIL, payload: error.response.data.message });
    }
};

