import React, { useEffect, useState } from 'react'
import { Avatar, Box, Button, TextareaAutosize, styled, } from '@mui/material'
import { useSelector } from "react-redux";
import SendIcon from '@mui/icons-material/Send';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import axios from 'axios';
import Comment from './Comment';
import './comment.css'
import Loader from '../../loader/Loader';

const Container = styled(Box)`
   margin-top:100px;
   display: flex;
`;

const StyledTextArea = styled(TextareaAutosize)`
   height: 100px;
   width: 100%;
   margin: 0 25px;
   padding: 12px;
   font-size: 16px;
   &:focus-visible {
    outline: none;
}
`

const Comments = ({ post }) => {
    const { user } = useSelector((state) => state.user);
    const url = "https://images.pexels.com/photos/1858175/pexels-photo-1858175.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500"

    const [comment, setComment] = useState('');
    const [comments, setComments] = useState([]);
    const [toggle, setToggle] = useState(false);

    const handleCommentSubmit = async (e) => {
        e.preventDefault();
        const formData = new FormData();
        formData.append("comments", comment);
        formData.append('postId', post?._id);
        formData.append('username', user?.username);
        formData.append('senderProfile', user?.profile);

        setToggle(prev => !prev);  // state reset
        try {
            const res = await axios.post('/api/v1/comments', formData, {
                headers: {
                    "Content-Type": "application/json"
                }
            })
            toast.success(res.data.message)
            window.location.reload('/post/')
        } catch (error) {
            toast.error(error.response.data.message)
        }

    }

    // get All comments
    useEffect(() => {
        const fetchComments = async () => {
            const response = await axios.get(`/api/v1/comments/${post?._id}`)
            setComments(response.data.comment)
        }
        fetchComments()
    }, [post?._id, toggle])

    return (
        <>
            <Box style={{ marginTop: '90px' }}>
                <div style={{ color: '#878787', marginBottom: '-6%' }}><h1>Comments</h1></div>
                <form onSubmit={handleCommentSubmit}>
                    <Container >
                        <Avatar style={{ width: 50, height: 50, marginTop: '10px' }} src={user?.profile || url} alt={post?.senderId?.username}></Avatar>
                        <StyledTextArea
                            minRows={3}
                            placeholder="What's on your mind?"
                            onChange={(e) => setComment(e.target.value)}
                        />
                        <Button variant="contained"
                            endIcon={<SendIcon />}
                            type='submit'
                            id='myForm'
                            style={{ height: 40, backgroundColor: 'teal', fontSize: '1.4rem', marginTop: '17px' }}
                        >
                        </Button>
                    </Container>
                </form>
                <Box className="reviews">
                    {
                        comments && comments.length > 0 ? comments.map(comment => (
                            <Comment comment={comment} setToggle={setToggle} />
                        )) : <Box style={{ color: '#878787', margin: '30px 80px', fontSize: 18 }} >
                            No Comments Yet  </Box>
                    }
                </Box>
            </Box>
            <ToastContainer
                position="bottom-right"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="dark"
            />
        </>
    )
}

export default Comments