import React, { useEffect, useState } from 'react'
import { Avatar, Button, Grid, Paper, Typography } from '@mui/material'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Input from '@mui/material/Input';
import InputAdornment from '@mui/material/InputAdornment';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import IconButton from '@mui/material/IconButton';
import { Link, useNavigate } from 'react-router-dom';
import MetaData from '../../MetaData/MetaData'
import { useDispatch, useSelector } from "react-redux";
import { clearErrors, updatePassword } from "../../actions/userAction";
import { UPDATE_PASSWORD_RESET } from "../../constants/userConstants";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './changePassword.css'

const ChangePassword = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { error, isUpdated, loading } = useSelector((state) => state.profile);

  const [showPassword, setShowPassword] = React.useState(false);
  const handleClickShowPassword = () => setShowPassword((show) => !show);
  const handleMouseDownPassword = (event) => { event.preventDefault() };
  const loginStyle = { display: 'flex', alignItems: 'center', justifyContent: 'center' }
  const paperStyle = { padding: 20, width: 300, display: 'flex', margin: '20px auto', flexDirection: 'column' }
  const GridStyle = { margin: '20px auto', alignItems: 'center' }
  const AvatarStyle = { backgroundColor: 'teal', marginLeft: '58px' }
  const TitleStyle = { marginTop: '20px', fontSize: '24px', color: ' rgb(1, 114, 114)', fontWeight: '600', fontFamily: 'sans-serif' }
  const InputStyle = { marginTop: '20px', fontSize: '17px', color: '#000', fontWeight: '500' }
  const ButtonStyled = { marginTop: '15px', fontSize: '15px', fontWeight: '600', backgroundColor: ' teal' }

  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  const handleUpdatePassword = (e) => {
    e.preventDefault();
    const myFrom = new FormData();
    myFrom.set("oldPassword", oldPassword);
    myFrom.set("newPassword", newPassword);
    myFrom.set("confirmPassword", confirmPassword);
    dispatch(updatePassword(myFrom));
  }

  useEffect(() => {
    if (error) {
      toast.error(error);
      console.log(error, "error")
      dispatch(clearErrors());
    }
    if (isUpdated) {
      toast.success("Profile Updated Successfully");
      navigate('/profile')
      dispatch({
        type: UPDATE_PASSWORD_RESET,
      });
    }
  }, [dispatch, error, isUpdated]);

  return (
    <>
      <MetaData title="Change Password" />
      <Grid item xs={12} sm={6} md={4} style={loginStyle} className="MobileLogin" >
        <Paper elevation={10} style={paperStyle}>
          <Grid style={GridStyle}>
            <Avatar style={AvatarStyle}><LockOutlinedIcon style={{ fontSize: '2.4rem' }} /></Avatar>
            <h2 style={TitleStyle}>Update Profile</h2>
          </Grid>
          <form onSubmit={handleUpdatePassword}>
            <Input
              style={InputStyle}
              label='Password'
              placeholder='Enter Your Old Password...'
              fullWidth
              required
              autoComplete="new-password"
              value={oldPassword}
              onChange={(e) => setOldPassword(e.target.value)}
            />
            <Input
              style={InputStyle}
              label='Password'
              placeholder='Enter Your New Password...'
              fullWidth
              required
              autoComplete="new-password"
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
            />
            <Input
              style={InputStyle}
              label='confirmPassword'
              placeholder='Enter Your Confirm Password...'
              fullWidth
              type={showPassword ? 'text' : 'password'}
              required
              autoComplete="new-password"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={handleClickShowPassword}
                    onMouseDown={handleMouseDownPassword}
                    edge="end"
                  >
                    {showPassword ? <VisibilityOff style={{ fontSize: '2.3rem' }} /> : <Visibility style={{ fontSize: '2.4rem' }} />}
                  </IconButton>
                </InputAdornment>
              }
            />
            <Button
              style={ButtonStyled}
              fullWidth
              type='submit'
              color="primary"
              variant='contained'>
              Change Password
            </Button>
          </form>
            <Link to='/profile' style={{ fontSize: '1.4rem', color:'teal', marginTop:'12px' }} >Don't want to update profile password ?</Link>
        </Paper>
      </Grid>
      <ToastContainer
        position="bottom-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="dark"
      />
    </>
  )
}

export default ChangePassword

