import React, { useEffect, useState } from 'react'
import { Avatar, Button, Grid, Paper, Typography, Box } from '@mui/material'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Input from '@mui/material/Input';
import InputAdornment from '@mui/material/InputAdornment';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import IconButton from '@mui/material/IconButton';
import { Link, useNavigate } from 'react-router-dom';
import MetaData from '../../MetaData/MetaData'
import Loader from '../../Components/loader/Loader'
import { useDispatch, useSelector } from "react-redux";
import { clearErrors, userRegister } from "../../actions/userAction";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Register = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { error, loading, isAuthenticated } = useSelector((state) => state.user);

  const paperStyle = { padding: 20, width: 300, display: 'flex', margin: '20px auto', flexDirection: 'column' }
  const loginStyle = { display: 'flex', alignItems: 'center', justifyContent: 'center' }
  const GridStyle = { margin: '20px auto', alignItems: 'center' }
  const AvatarStyle = { backgroundColor: 'teal', marginLeft: '68px', }
  const TitleStyle = { marginTop: '20px', fontSize: '24px', color: ' rgb(1, 114, 114)', fontWeight: '600', fontFamily: 'sans-serif' }
  const InputStyle = { marginTop: '20px', fontSize: '17px', color: '#000', fontWeight: '500' }
  const ButtonStyled = { marginTop: '15px', fontSize: '15px', fontWeight: '600', backgroundColor: ' teal' }
  const ProfileImage = { width: '75px', height: '75px', borderRadius: '50%', cursor: 'pointer', objectFit: 'cover', marginLeft: '36%' }
  const url = "https://images.pexels.com/photos/1858175/pexels-photo-1858175.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500"

  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword((show) => !show);
  const handleMouseDownPassword = (event) => { event.preventDefault() };

  const [profile, setProfile] = useState('');
  const [filename, setFilename] = useState('https://images.pexels.com/photos/1858175/pexels-photo-1858175.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500');
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const registerDataChange = (e) => {
    e.preventDefault();
    setProfile(e.target.files[0]);
    setFilename(e.target.files[0].name);
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    const formData = new FormData();
    const filename = Date.now() + profile.name
    formData.append("profile", profile);
    formData.append("filename", filename);
    formData.append('username', username)
    formData.append('email', email);
    formData.append('password', password);
    dispatch(userRegister(formData));
  }
  useEffect(() => {
    if (error) {
      toast.error(error);
      dispatch(clearErrors());
    }
    if (isAuthenticated) {
      navigate('/profile');
    }
  }, [dispatch, error, isAuthenticated]);

  return (
    <>
      <MetaData title="Register" />
      {loading ? (
        <Loader />
      ) : (
        <>
          <Grid item xs={12} sm={6} md={4} style={loginStyle} className="MobileLogin">
            <Paper elevation={10} style={paperStyle}>
              <Grid style={GridStyle}>
                <Avatar style={AvatarStyle}><LockOutlinedIcon style={{ fontSize: '2.4rem' }} /></Avatar>
                <h2 style={TitleStyle}>Create Account</h2>
              </Grid>
              <form
                onSubmit={handleSubmit}
                encType="multipart/form-data">
                <Box className="settingsPP" style={{ alignItems: 'center', justifyContent: 'center' }}>
                  <label htmlFor="fileInput">
                    {profile && (<img src={URL.createObjectURL(profile)} style={ProfileImage} />) || <img src={url} alt="profile photo" style={ProfileImage}/>}
                  </label>
                  <Input
                    id="fileInput"
                    type="file"
                    style={{ display: "none" }}
                    className="settingsPPInput"
                    accept="image/*"
                    onChange={registerDataChange}
                  />
                </Box>
                <Input
                  style={InputStyle}
                  label='username'
                  placeholder='Enter Your Username...'
                  type='name'
                  fullWidth
                  required
                  name='username'
                  onChange={(e) => setUsername(e.target.value)}
                />
                <Input
                  style={InputStyle}
                  label='Email'
                  placeholder='Enter Your Email...'
                  type='email'
                  fullWidth
                  required
                  name='email'
                  onChange={(e) => setEmail(e.target.value)}
                />
                <Input
                  style={InputStyle}
                  label='Password'
                  placeholder='Enter Your Password...'
                  fullWidth
                  type={showPassword ? 'text' : 'password'}
                  required
                  name='password'
                  onChange={(e) => setPassword(e.target.value)}
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleClickShowPassword}
                        onMouseDown={handleMouseDownPassword}
                        edge="end"
                      >
                        {showPassword ? <VisibilityOff style={{ fontSize: '2.3rem' }} /> : <Visibility style={{ fontSize: '2.4rem' }} />}
                      </IconButton>
                    </InputAdornment>
                  }
                />
                <Button style={ButtonStyled} fullWidth type='submit' variant='contained'>Sign Up </Button>
                <Typography style={{ fontSize: '13px', marginTop: '12px', fontWeight: '600' }} className="MobileText">
                  Do you have an account ?<Link style={{ marginLeft: '8px', color:'teal' }} to='/login'>
                    Sign In
                  </Link>
                </Typography>
                <Typography style={{ fontSize: '13px', marginTop: '12px', fontWeight: '600' }} className="MobileText">
                  Lost your account ?<Link style={{ marginLeft: '8px', color:'teal' }} to='/recover/account'>
                    Recover Account
                  </Link>
                </Typography>
              </form>
            </Paper>
          </Grid>
        </>
      )}
      <ToastContainer
        position="bottom-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="dark"
      />
    </>

  )
}
export default Register