import React, { useEffect, useState } from 'react'
import { Avatar, Button, Grid, Paper, Typography } from '@mui/material'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Input from '@mui/material/Input';
import { Link, useNavigate } from 'react-router-dom';
import MetaData from '../../MetaData/MetaData'
import { useDispatch, useSelector } from "react-redux";
import { clearErrors, recoverAccount } from "../../actions/userAction";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Loader from '../../Components/loader/Loader';
import './account.css'

const RecoverAccount = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const { error, message, loading, isAuthenticated } = useSelector((state) => state.forgotPassword);

    const loginStyle = { display: 'flex', alignItems: 'center', justifyContent: 'center' }
    const paperStyle = { padding: 20, width: 300, display: 'flex', margin: '20px auto', flexDirection: 'column' }
    const GridStyle = { margin: '20px auto', alignItems: 'center' }
    const AvatarStyle = { backgroundColor: 'teal', marginLeft: '80px' }
    const TitleStyle = { marginTop: '20px', fontSize: '24px', color: ' rgb(1, 114, 114)', fontWeight: '600', fontFamily: 'sans-serif' }
    const InputStyle = { marginTop: '20px', fontSize: '17px', color: '#000', fontWeight: '500' }
    const ButtonStyled = { marginTop: '15px', fontSize: '15px', fontWeight: '600', backgroundColor: ' teal' }

    const [email, setEmail] = useState(null);
    const recoverAccountSubmit = (e) => {
        e.preventDefault();
        const myForm = new FormData();
        myForm.set("email", email);
        dispatch(recoverAccount(myForm));
    };

    useEffect(() => {

        if (error) {
            toast.error(error);
            dispatch(clearErrors());
        }

        if (message) {
            toast.success(message);

            // setTimeout(() => {
            // window.location.reload('/recover/account')
            // navigate('recover/account')
            // }, 5000);
        }
    }, [dispatch, error, message, isAuthenticated]);

    return (
        <>
            <MetaData title="Recover Account" />
            {loading ? (
                <Loader />
            ) : (
                <Grid style={loginStyle} className="MobileLogin">
                    <Paper elevation={10} style={paperStyle}>
                        <Grid style={GridStyle}>
                            <Avatar style={AvatarStyle}><LockOutlinedIcon style={{ fontSize: '2.4rem' }} /></Avatar>
                            <h2 style={TitleStyle} >Recover Account</h2>
                        </Grid>
                        <form onSubmit={recoverAccountSubmit}>
                            <Input
                                style={InputStyle}
                                label='Email'
                                placeholder='Enter Your Email...'
                                type='email'
                                fullWidth
                                required
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                            />

                            <Button
                                style={ButtonStyled}
                                fullWidth
                                type='submit'
                                variant='contained'>
                                Send
                            </Button>

                            <Typography
                                style={{ fontSize: '13px', marginTop: '10px', fontWeight: '600' }}
                                className="MobileText">
                                Do you have an account ?
                                <Link to='/login'
                                    style={{ marginLeft: '8px',color:'teal' }}>
                                    Sign In
                                </Link>
                            </Typography>
                        </form>
                    </Paper>
                </Grid>
            )}
            <ToastContainer
                position="bottom-right"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="dark"
            />
        </>
    )
}

export default RecoverAccount