import React, { useEffect, useState } from 'react'
import { Avatar, Button, Grid, Paper, Typography } from '@mui/material'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Input from '@mui/material/Input';
import InputAdornment from '@mui/material/InputAdornment';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import IconButton from '@mui/material/IconButton';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import MetaData from '../../MetaData/MetaData'
import { useDispatch, useSelector } from "react-redux";
import { clearErrors, resetPassword } from "../../actions/userAction";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './forgot.css'

const ResetPassword = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const { error, success, loading } = useSelector((state) => state.forgotPassword);

    const [showPassword, setShowPassword] = React.useState(false);
    const handleClickShowPassword = () => setShowPassword((show) => !show);
    const handleMouseDownPassword = (event) => {event.preventDefault();};
    const loginStyle = { display: 'flex', alignItems: 'center', justifyContent: 'center' }
    const paperStyle = {backgroundColor:'white', padding: 20, width: 300, display: 'flex', margin: '20px auto', flexDirection: 'column' }
    const GridStyle = { margin: '20px auto', alignItems: 'center' }
    const AvatarStyle = { backgroundColor: 'teal', marginLeft: '85px' }
    const TitleStyle = { marginTop: '20px', fontSize: '24px', color: ' rgb(1, 114, 114)',  fontWeight: '600',fontFamily: 'sans-serif' }
    const InputStyle = { marginTop: '20px', fontSize: '17px', color: '#000', fontWeight: '500' }
    const ButtonStyled = { marginTop: '15px', fontSize: '15px', fontWeight: '600', backgroundColor: ' teal'  }

    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const location = useLocation();
    const token = location.pathname.split("/")[5]

    const resetPasswordSubmit = (e) => {
        e.preventDefault();
        const myForm = new FormData();
        myForm.set("password", password);
        myForm.set("confirmPassword", confirmPassword);
        dispatch(resetPassword(token, myForm));
      };
    
      useEffect(() => {
        if (error) {
            toast.error(error);
          dispatch(clearErrors());
        }
        if (success) {
          toast.success("Password Updated Successfully");
    
          navigate('/login');
        }
      }, [dispatch, error, success]);

    return (
        <>
         <MetaData title="Reset Password" />
        <Grid item xs={12} sm={6} md={4} style={loginStyle} className="MobileLogin" >
            <div style={paperStyle}>
                <Grid style={GridStyle}>
                    <Avatar style={AvatarStyle}><LockOutlinedIcon style={{ fontSize: '2.4rem' }} /></Avatar>
                    <h2 style={TitleStyle} >Confirm Password</h2>
                </Grid>
                <form  onSubmit={resetPasswordSubmit}>
                    <Input
                        style={InputStyle}
                        label='Password'
                        placeholder='Enter Your New Password...'
                        fullWidth
                        required
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        autoComplete="new-password"
                    />
                    <Input
                        style={InputStyle}
                        label='confirmPassword'
                        placeholder='Enter Your Confirm Password...'
                        fullWidth
                        type={showPassword ? 'text' : 'password'}
                        required
                        value={confirmPassword}
                        onChange={(e) => setConfirmPassword(e.target.value)}
                        autoComplete="new-password"
                        endAdornment={
                            <InputAdornment position="end">
                                <IconButton
                                    aria-label="toggle password visibility"
                                    onClick={handleClickShowPassword}
                                    onMouseDown={handleMouseDownPassword}
                                    edge="end"
                                >
                                    {showPassword ? <VisibilityOff style={{ fontSize: '2.3rem' }} /> : <Visibility style={{ fontSize: '2.4rem' }} />}
                                </IconButton>
                            </InputAdornment>
                        }
                    />
                    <Button
                        style={ButtonStyled}
                        fullWidth
                        type='submit'
                        color="primary"
                        variant='contained'>
                        Confirm Password
                    </Button>
                    <Typography
                        style={{ fontSize: '13px', marginTop: '10px', fontWeight: '600' }}
                        className="MobileText">
                        Do you have an account ?
                        <Link to='/login'
                            style={{ marginLeft: '8px',color:'teal' }}>
                            Sign In
                        </Link>
                    </Typography>
                </form>
            </div>
        </Grid>
        <ToastContainer
                position="bottom-right"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="dark"
            />
        </>
    )
}

export default ResetPassword