import React from "react";
import { Typography, Box } from "@mui/material";
import { Link } from "react-router-dom";
import MapsHomeWorkIcon from '@mui/icons-material/MapsHomeWork';
import PhoneIcon from '@mui/icons-material/Phone';
import MailOutlinedIcon from '@mui/icons-material/MailOutlined';
import FacebookIcon from '@mui/icons-material/Facebook';
import TwitterIcon from '@mui/icons-material/Twitter';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';
import LanguageIcon from '@mui/icons-material/Language';
import './footer.css';


// Footer date 
const date = new Date()
const year = date.getFullYear()

const Footer = () => {

    return (
        <>
            <footer className="footer-distributed">
                <Box className="footer-left">
                    <h3><Link to="/">B<span>log</span></Link> </h3>
                    <Typography className="footer-links">
                        <Link to="/" className="link-1">Home</Link>
                        <Link to="/about">About</Link>
                        <Link to="/write">Write</Link>
                        <Link to="/contact">Contact</Link>
                        <Link to="/terms">Terms</Link>
                        <Link to="/privacy">Privacy Policy</Link>
                    </Typography>
                    <Typography className="footer-company-name">Copyright &#169; {year} {" "} <Link to='/' style={{ color: '#878787' }} >Blog</Link> | All Rights Reserved</Typography>
                </Box>
                <Box className="footer-center">
                    <Box>
                        <MapsHomeWorkIcon className="icon" />
                        <p><span>444 S. Cedros Ave</span> Solana Beach, California</p>
                    </Box>
                    <Box>
                        <PhoneIcon className="icon" />
                        <p>+1.555.555.5555</p>
                    </Box>
                    <Box>
                        <MailOutlinedIcon className="icon" />
                        <p><a href="https://mail.google.com/mail/u/0/#inbox?compose=new" target="_blank" rel="noreferrer nofollow" >officialblogweb@gmail.com</a></p>
                    </Box>
                    
                </Box>
                <Box className="footer-right">
                    <Typography className="footer-company-about">
                        <span>About</span>
                        Blog is a Professional Blog Platform. Here we will provide you only interesting content, which you will like very much.
                    </Typography>
                    <Box className="footer-icons">
                        <a href="https://www.linkedin.com/in/sanjeev-kumar-249149144" rel="noreferrer nofollow" ><FacebookIcon className="footerIcon" /></a>
                        <a href="https://github.com/sanjeev071997" rel="noreferrer nofollow" ><TwitterIcon className="footerIcon" /></a>
                        <a href="https://www.linkedin.com/in/sanjeev-kumar-249149144" target="_blank"  rel="noreferrer nofollow" ><LinkedInIcon className="footerIcon" /></a>
                        <a href="https://github.com/sanjeev071997" target="_blank" rel="noreferrer nofollow" ><GitHubIcon className="footerIcon" /></a>
                    </Box>
                </Box>
                
                    <Typography className='Portfolio'>
                      <LanguageIcon  className="PortfolioIcon" />  
                      <a href="https://sanjeevkumarsk.web.app/" target="_blank" rel="noreferrer nofollow" >Visit My Portfolio </a>
                    </Typography>
            </footer>
        </>
    )
}
export default Footer;

