import React, { useEffect, useState } from "react";
import MetaData from "../../MetaData/MetaData";
import { Link, useNavigate } from 'react-router-dom';
import { Avatar, Button, Container, Input, InputBase } from '@mui/material'
import { useDispatch, useSelector } from "react-redux";
import { clearErrors, reloadUser, updateProfile, deleteUser } from "../../actions/userAction";
import { UPDATE_PROFILE_RESET } from '../../constants/userConstants';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './profile.css'


const Profile = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  // get the user information
  const { user } = useSelector((state) => state.user);
  const { error, isUpdated, loading, isAuthenticated } = useSelector((state) => state.profile);
  const TitleStyle = { marginTop: '20px', fontSize: '24px', color: ' rgb(1, 114, 114)', fontWeight: '600', fontFamily: 'sans-serif' }

  useEffect(() => {
    if (isAuthenticated === false) {
      navigate("/login");
    }
  }, [isAuthenticated]);

  // delete user (profile)
  const handleDeleteUser = () => {
    dispatch(deleteUser(user._id))
    toast.success('Delete Account Successfully');
    dispatch(reloadUser())
    navigate('/register');
  }

  // update profile 
  const [profile, setProfile] = useState(null);
  const [filename, setFilename] = useState('');
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [updateMode, setUpdateMode] = useState(false)
  const [avatarPreview, setAvatarPreview] = useState("");

  const profileUpdate = (e) => {
    setProfile(e.target.files[0]);
    setFilename(e.target.files[0].name);
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        setAvatarPreview(reader.result);
      }
    };
    reader.readAsDataURL(e.target.files[0]);
  }

  const handleProfileSubmit = async (e) => {
    e.preventDefault();
    const formData = new FormData();
    const filename = Date.now() + profile.name
    formData.append("profile", profile);
    formData.append("filename", filename);
    formData.append('username', username)
    formData.append('email', email);
    dispatch(updateProfile(formData));
  }

  useEffect(() => {
    if (user) {
      setUsername(user.username);
      setEmail(user.email);
      setProfile(user.profile)
    }
    if (error) {
      toast.error(error);
      dispatch(clearErrors());
    }
    if (isUpdated) {
      toast.success("Profile Updated Successfully");
      dispatch(reloadUser())
      navigate('/profile');
      dispatch({ type: UPDATE_PROFILE_RESET });
    }
  }, [dispatch, error, user, isUpdated]);

  const url = "https://images.pexels.com/photos/1858175/pexels-photo-1858175.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500"

  return (
    <>
      {updateMode ? (
        <>
          <Container>
            <MetaData title={`${user.username}'s Edit-Profile`} />
            <h1 style={TitleStyle}>Edit Profile</h1>
          </Container>
        </>
      ) : (
        <>
          <Container>
            <MetaData title={`${user.username}'s Profile`} />
            <h1 style={TitleStyle} >My Profile</h1>
          </Container>
        </> 
      )}
      <Container>
        <form
          onSubmit={handleProfileSubmit}
          encType="multipart/form-data">
          <div className="profileContainer">
            <div>
              {updateMode ? ( 
                <label htmlFor="fileInput">
                  <Avatar src={avatarPreview} className="updateProfileImageAvatar">
                  <img src={user.profile} className="updateProfileImageAvatar" />
                  </Avatar>
              </label>
              ): (
                <img src={user.profile} className="updateProfileImage" />
              )}
              {updateMode ? (
                <Button className="profileEdit" type='submit' variant='contained'> Update Profile </Button>
                ) : (
                  <Button className="profileEdit" variant='contained'> <Link style={{ color: 'inherit' }} onClick={() => setUpdateMode(true)} > Edit Profile</Link></Button>
                  )}
            </div>
            {updateMode ? (
              <>
                <Input
                  id="fileInput"
                  type="file"
                  style={{ display: "none" }}
                  className="settingsPPInput"
                  accept="image/*"
                  onChange={profileUpdate}
                />
                <div>
                  <div>
                    <h4>Username</h4>
                    <p>
                      <InputBase
                        label='username'
                        placeholder='Enter Your Username...'
                        type='name'
                        fullWidth
                        required
                        name='username'
                        value={username}
                        onChange={(e) => setUsername(e.target.value)}
                        className="profileInput"
                      />
                    </p>
                  </div>
                  <div>
                    <h4>Email</h4>
                    <p>
                      <InputBase
                        label='Email'
                        placeholder='Enter Your Email...'
                        type='email'
                        fullWidth
                        required
                        name='email'
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        className="profileInput"
                      />
                    </p>
                  </div>
                  <div>
                    <h4>Joined On</h4>
                    <p>{String(user.createdAt).substr(0, 10)}</p>
                  </div>
                  <div>
                    <Button className="profileButton" fullWidth variant='contained'><Link to='/profile/change/password' style={{ color: 'inherit' }}>Change Password</Link> </Button>
                    <Button className="profileDelete" fullWidth variant='contained' onClick={handleDeleteUser} >Delete Account</Button>
                  </div>
                </div>
              </>
            ) : (
              <>
                <div>
                  <div>
                    <h4>Username</h4>
                    <p>{user.username}</p>
                  </div>
                  <div>
                    <h4>Email</h4>
                    <p>{user.email}</p>
                  </div>
                  <div>
                    <h4>Joined On</h4>
                    <p>{String(user.createdAt).substr(0, 10)}</p>
                  </div>
                  <div>
                    <Button className="profileButton" fullWidth variant='contained'><Link to='/profile/change/password' style={{ color: 'inherit' }} >Change Password</Link> </Button>
                    <Button className="profileDelete" fullWidth variant='contained' onClick={handleDeleteUser} >Delete Account</Button>
                  </div>
                </div>
              </>
            )}
          </div>
        </form>
      </Container>
      <ToastContainer
        position="bottom-center"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick={false}
        rtl={false}
        pauseOnFocusLoss={false}
        draggable={false}
        pauseOnHover={false}
        theme="colored"
      />
    </>
  )
}

export default Profile