import React from 'react'
import { Grid, Typography, Box, Container } from '@mui/material'
import MetaData from '../../MetaData/MetaData'

const About = () => {
  const ImageStyled = { height: '400px', display: 'flex', width: '80%', marginTop: '20px', cursor: 'pointer', }
  const TitleStyle = { marginTop: '20px', fontSize: '24px', color: ' rgb(1, 114, 114)', fontWeight: '600', fontFamily: 'sans-serif' }

  const url = 'https://sanjeevkumarsk.web.app/images/about.png'

  return (
    <>
      <MetaData title="About" />
      <Container>
        <h2 style={TitleStyle} >About Us! </h2>
        <Grid container>
          <Grid item xs={12} sm={12} md={6} lg={6}>
            <img
              src={url}
              alt="about image"
              style={ImageStyled} />
          </Grid>
          <Grid item xs={12} sm={12} md={6} lg={6}>
            <Container style={{ fontSize: "1.4rem" }}>
              <h3 style={{ textAlign: "center", fontSize: "22px", marginTop: '20px' }}>Welcome To <span id="W_Name1">Blog</span></h3>
              <Box style={{ marginTop: '20px' }}>
                <Typography style={{ fontSize: "1.4rem" }}><span id="W_Name2">Blog</span> is a Professional <span id="W_Type1">Blog</span> Platform. Here we will provide you only interesting content, which you will like very much. We're dedicated to providing you the best of <span id="W_Type2">Blog</span>, with a focus on dependability and <span id="W_Spec">Blog</span>. We're working to turn our passion for <span id="W_Type3">Blog</span> into a booming  We hope you enjoy our <span id="W_Type4">Blog</span> as much as we enjoy offering them to you.</Typography>
                <Typography style={{ fontSize: "1.4rem" }}>I will keep posting more important posts on my Website for all of you. Please give your support and love.</Typography>
                <Typography style={{ fontWeight: "bold", textAlign: "center", fontSize: "18px", marginTop: '20px' }}>Thanks For Visiting Our Site<br /><br />
                  <span style={{ color: "teal", fontSize: "18px", fontWeight: "bold", textAlign: "center" }}>Have a nice day!</span></Typography>
              </Box>
            </Container>
          </Grid>
        </Grid>
      </Container>
    </>
  )
}

export default About