import React, { useState } from 'react'

import SearchIcon from '@mui/icons-material/Search';
import { styled, alpha } from '@mui/material/styles';
import InputBase from '@mui/material/InputBase';
import CloseIcon from '@mui/icons-material/Close';
import DehazeIcon from '@mui/icons-material/Dehaze';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from "react-redux";
import { logout } from '../../actions/userAction';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import "./topbar.css";

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(1),
    width: 'auto',
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  },
}));
const Topbar = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { isAuthenticated, user } = useSelector((state) => state.user);
  const [showMenu, setShowMenu] = useState(false)

  const handleLogout = () => {
    dispatch(logout())
    toast.success('Logout Successfully');
    navigate('/login');
  }


  return (
    <>
      <nav className='main-nav'>
        <div className='logo'>
          <h2>
            <Link to="/"><span>B</span>log</Link>
          </h2>
        </div>
        <div className={showMenu ? "menu-link mobile-menu-link" : "menu-link"} onClick={() => !setShowMenu(false)}>
          <ul>
            <li>
              <Link to='/'>Home</Link>
            </li>
            <li>
              <Link to='/about'>About</Link>
            </li>
            <li>
              <Link to='/write'>Write</Link>
            </li>
            <li>
              <Link to='/contact'>Contact</Link>
            </li>
            {isAuthenticated ? (
              <li>
                {isAuthenticated && <li onClick={handleLogout} ><Link to="#">{isAuthenticated && "Logout"}</Link></li>}
              </li>
            ) : (
              // <li className='DesktopLoginStyle'>
              //   <Link to='/login' style={{ marginRight: '12px' }}>Login</Link>
              // </li>
              <></>
              
            )}
            <li>
            </li>
          </ul>
        </div>
        {/* search and profile pic */}
        <div className='profile profile-desktop'>
          <ul>
            <li>
         {/* <Link to="#"> <SearchIcon className="topImg" style={{ fontSize: '2.3rem' }} /></Link>  */}
            </li>
            {isAuthenticated ? (
              <>
              <li>
                <Link to="/profile">
                  <img className="topImg" src={user?.profile || 'https://images.pexels.com/photos/1858175/pexels-photo-1858175.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500'}
                    alt="profile photo" title='profile' />
                </Link>
              </li>
            </>
            ) : (
              <li className='MobileLoginStyle'>
                <Link to='/login' style={{ marginRight: '12px' }}>Login</Link>
              </li>
            )}
          </ul>
          {/* Hamburger menu start */}
          <div className='hamburger-menu'>
            <Link className='hamburger-menu' to="#" onClick={() => setShowMenu(!showMenu)}>
              {showMenu ? <CloseIcon style={{ fontSize: '3rem', color: 'teal' }} /> : <DehazeIcon style={{ fontSize: '3rem', color: 'teal' }} />}
            </Link>
          </div>
        </div>
      </nav>
      {/* <ToastContainer
       position="bottom-center"
       autoClose={5000}
       hideProgressBar={false}
       newestOnTop={false}
       closeOnClick={false}
       rtl={false}
       pauseOnFocusLoss={false}
       draggable={false}
       pauseOnHover={false}
       theme="colored"
       /> */}
    </>
  )
}

export default Topbar





