import React, { useEffect } from 'react';
import { BrowserRouter, Routes, Route, } from 'react-router-dom';
import { useSelector } from "react-redux";
import Topbar from './Components/topbar/Topbar';
import About from './pages/about/About';
import Contact from './pages/contact/Contact';
import Home from './pages/home/Home';
import Login from './pages/login/Login';
import Register from './pages/register/Register';
import Single from './pages/single/Single';
import Write from './pages/write/Write';
import ForgotPassword from './pages/forgotpassword/ForgotPassword';
import ResetPassword from './pages/forgotpassword/ResetPassword';
import Footer from './Components/footer/Footer';
import GoToTop from './Components/gototopbutton/GoToTop';
import Terms from './pages/terms/Terms';
import PrivacyPolicy from './pages/privacyPolicy/PrivacyPolicy';
import Profile from './pages/profile/Profile';
import store from './store';
import { reloadUser } from './actions/userAction';
import ChangePassword from './pages/profile/ChangePassword';
import RecoverAccount from './pages/account/RecoverAccount';
import RecoverAccountLogin from './pages/account/RecoverAccountLogin';

function App() {
  const { isAuthenticated } = useSelector((state) => state.user);
  useEffect(() => {
    store.dispatch(reloadUser());
  }, []);

  return (
    <>
      <BrowserRouter>
        <Topbar />
        <Routes>
          <Route path='/' element={<Home />} />
          <Route path='/about' element={<About />} />
          <Route path='/post/:postId' element={<Single />} />

          <Route path='/terms' element={<Terms />} />
          <Route path='/privacy' element={<PrivacyPolicy />} />
          <Route path='/contact' element={<Contact /> } />

          <Route path='/register' element={<Register />} />
          <Route path='/login' element={<Login />} />

          <Route path='write' element={<Write /> } />

          <Route path='/forgot/password' element={<ForgotPassword />} />
          <Route path='/auth/password/reset/:token' element={<ResetPassword />} />
          <Route path='/profile/reset/password' element={isAuthenticated ? <ResetPassword /> : <Login />} />

          <Route path='/profile' element={isAuthenticated ? <Profile /> : <Login />} />
          <Route path='/profile/change/password' element={isAuthenticated ? <ChangePassword /> : <Login />} />

          <Route path='/recover/account' element={<RecoverAccount />} />
          <Route path='/recover/account/login/:token' element={<RecoverAccountLogin />} />
        </Routes>
        <GoToTop />
        <Footer />
      </BrowserRouter>
    </>
  );
}

export default App;
