import React from 'react'
import { useSelector } from "react-redux";
import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './comment.css'

const Comment = ({ comment, setToggle }) => {
  const { user } = useSelector((state) => state.user);
  const removeComment = async () => {
    try {
      const res = await axios.delete(`/api/v1/comments/delete/${comment._id}`)
      toast.success(res.data.message)
    } catch (error) {
      toast.error(error)
    }
    setToggle(prev => !prev);
  }


  return (
    <>
        <div className='reviewCard'>
          <img src={comment?.senderId?.profile} />
          <p>{comment?.senderId?.username}</p>
          <span className="reviewCardDate"> {new Date(comment?.createdAt).toDateString()}</span>
          <span className='reviewCardComment'> {comment?.comments}</span>
          {comment?.senderId?.username === user?.username && (
            <button className='reviewCardButton' onClick={() => removeComment()} >Delete</button>
          )}
        </div>
      <ToastContainer
        position="bottom-right" 
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="dark"
      />
    </>
  )
}


export default Comment